@import url("https://cdn.jsdelivr.net/npm/bootstrap-icons@1.11.1/font/bootstrap-icons.css");

.navbar{
    position: fixed;
    width: 100%;
}
.intro{
    background: url("https://images.unsplash.com/photo-1476842634003-7dcca8f832de?ixlib=rb-4.0.3&ixid=M3wxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8fA%3D%3D&auto=format&fit=crop&w=2070&q=80");
}
.container{
    font-family: poppins;
}
.section{
    font-family: poppins;
    min-height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 18px;
    
}

.avatar{
    width: 200px;
    border-radius: 300px;
}

.social-media{
    font-size: 20px;
    color: white;
    margin-left: 10px;
    margin-right: 10px;
}

.social-media:hover{
    transition: 0.5s;
    color: yellowgreen;
}

.hire{
    margin-top: 20px;
}

